import request from "@/utils/request"
import qs from "qs"
// 高校公益活动首页课程
export const collegeCourse = (params) => {
  return request.post('/school/home/college/courses', qs.stringify(params))
}

// 往期回顾新闻列表
export const collegeNews = ({ id, info }) => {
  return request.post(`/service/cms/article/article/list/${id}`, qs.stringify(info), {custom:{isAis:true}})
}

// 详情内容
export const newsDetail = ({ id, info }) => {
  return request.post(`/service/cms/article/article/details/${id}`, qs.stringify(info), {custom:{isAis:true}})
}
