import WeChatShare from "@/mixins/WeChatShare"
import { isWeChat } from 'ais-common-utils'
import { courseShareCallBack } from '@/api/course'
export default {
  mixins:[WeChatShare],
  props: {
    shareOption: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      data: [{ title: "wechat" }, { title: "qq" }, { title: "weibo" }],
      url: null,
      copyShow: false,
      isQQ: true,
      isWeChat:false, //判断是否微信
      show: false, //第三方分享弹窗显示
    }
  },
  mounted (){
    this.url = this.shareOption.link || window.location.href
    this.isWeChat = isWeChat()
    if(this.isWeChat){
      this.getWechatShare(this.shareOption.courseId)
    }
  },
  methods: {
    open() {
      this.show = true
    },
    //课程分享回调
    courseShareCallBack() {
      if(this.shareOption.courseId) courseShareCallBack(this.shareOption.courseId)
    },
    onCopySuccess() {
      this.$toast.success("复制成功")
      this.copyShow = false
      //课程分享回调
      this.courseShareCallBack()
    },
    //分享图标点击
    itemClick(value) {
      if (value === "wechat" || value === "qq") {
        this.copyShow = true
        this.isQQ = value === "qq"
      }
      if (value === "weibo") {
        const share = {
          title: this.shareOption.title,
          image_url: this.shareOption.imgUrl,
          share_url: window.location.href
        }
        location.replace(
          "https://service.weibo.com/share/share.php?url=" +
            encodeURIComponent(share.share_url) +
            "&title=" +
            share.title +
            "&pic=" +
            share.image_url +
            "&searchPic=true"
        )
        //课程分享回调
        this.courseShareCallBack()
      }
      this.show = false
    }
  }
}
