import {newsDetail} from "@/api/college"
import config from "@/config/common.config"
import ThirdShare from '@/components/Common/ThirdShare'
export default {
  components: { ThirdShare },
  async asyncData (ctx) {
    const {data} = await newsDetail({id: +ctx.route.params.id, info: { correlation:true }})
    if(data.code === 0){
      return {
        data:data.data,
        shareOption:{
          title : data.data.entry.title,
          desc: data.data.entry.description,
          imgUrl: `${config.IMG_URL.default}${data.data.entry.poster}`,
          link: `${config.LINK.school_h5}/newsDetail/${+ctx.route.params.id}`,
        },
      }
    }
  },
  methods: {
    //打开分享窗口
    openShare(){
      this.$refs.ThirdShare.open()
    },
    handelGo(info){
      if(info){
        this.$router.replace(`/newsDetail/${info.id}`)
      }
    }
  },
  head () {
    return {
      title: `${this.data.entry.title}—艾思云课堂`,
      meta: [
        { hid: 'description', name: 'description', content: this.data.entry.description },
        { hid: 'keywords', name: 'keywords', content: this.data.entry.keywords }
      ]
    }
  },
}
