import $config from '@/config/common.config'
import { getShareConfig, courseShareCallBack, getShareWXConfig } from "@/api/course"
import { dynamicScript } from 'ais-common-utils'
export default {
  methods:{
    /**微信分享 */
    async getWechatShare(courseId){
      await dynamicScript(`${$config.staticUrl}/jweixin-1.4.0.js`, 'wx')
      const params ={url:window.location.href}
      const {data} = this.isVideoWeChat ? await getShareWXConfig(params) : await getShareConfig(params)
      if(data.code === 0){
        window.wx.config({
          appId:data.data.appid,
          timestamp:data.data.timestamp,
          nonceStr:data.data.noncestr,
          signature:data.data.signature,
          jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'onMenuShareWeibo', 'onMenuShareQZone', 'showOptionMenu', 'hideAllNonBaseMenuItem', 'showAllNonBaseMenuItem']
        })
        window.wx.ready(()=>{
          //设置微信内QQ & 微信分享参数
          const share = {
            ...this.shareOption,
            success:() => {
              this.show = false
              //课程分享回调
              if(courseId) {
                courseShareCallBack(courseId)
              }
            },
            cancel:() => {
              this.show = false
            }
          }
          //自定义分享给朋友
          window.wx.onMenuShareAppMessage({
            ...share
          })
          //自定义分享给QQ
          window.wx.onMenuShareQQ({
            ...share
          })
          //自定义分享到腾讯微博
          window.wx.onMenuShareWeibo({
            ...share
          })
          //自定义分享到QQ空间
          window.wx.onMenuShareQZone({
            ...share
          })
          //自定义分享到朋友圈
          window.wx.onMenuShareTimeline({
            ...share
          })
          // //自定义“分享给朋友”及“分享到QQ”按钮的分享内容
          // window.wx.updateAppMessageShareData({ 
          //   ...share
          // })
          // //自定义“分享到朋友圈”及“分享到QQ空间”按钮的分享内容
          // window.wx.updateTimelineShareData({
          //   ...share
          // })
        })
      }
    }
  }
}
